.order {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &__title {
      font-size: 2vw;
    }
  }

  &__header {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    margin-bottom: 8px;

    &__text {
      display: flex;
      justify-content: center;
      font-size: 1vw;
      font-weight: bold;
      flex: 1;
    }
  }

  &__item {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    box-shadow: 0 1px 3px 0.2px;
    margin-bottom: 8px;

    &__text {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    &__button {
      flex: 1;
      display: flex;
      justify-content: center;
    }
  }
}
